@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700);
.search{}.search #wrap{position:relative;display:flex;width:100%;align-items:center;position:relative;height:62px;padding:1px 30px}.search .searchInput{height:60px;font-size:calc(10px + 3vmin);display:inline-block;font-family:'Lato';font-weight:400;border:none;outline:none;color:white;padding:3px;padding:0px 80px;width:0px;position:absolute;top:0;right:0;background:none;z-index:2;transition:width 0.4s cubic-bezier(1, 1, 1, 1);cursor:pointer}.search .searchInput.show{width:100%;z-index:1;cursor:text}.search .searchInput.show+.backArrow{left:30px;opacity:1;z-index:3;opacity:0.75}.search .backArrow{position:absolute;right:0;left:100%;opacity:0;transition:all 0.4s cubic-bezier(1, 1, 1, 1);top:56%;z-index:0;transform:translateY(-50%);cursor:pointer}.search img.search{height:calc(10px + 3vmin);width:calc(10px + 3vmin);min-height:30px;min-width:30px;display:inline-block;text-indent:-10000px;border:none;position:absolute;top:50%;right:30px;z-index:2;cursor:pointer;opacity:0.4;cursor:pointer;transition:opacity 0.3s ease-in;transform:translateY(-50%)}.search img.search:hover{opacity:0.8}

.header{width:100%;position:fixed;z-index:100;background-color:#161515;top:0;box-shadow:2px 6px 6px #161515}.header .header-items{position:relative}.header .header-items .logo{position:absolute;left:30px;top:50%;width:calc(10px + 3vmin);opacity:0.4;transform:translateY(-50%) scale(1.2);z-index:1;cursor:pointer;transition:all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86)}.header .header-items .logo:hover{opacity:1}.header .header-items .logo.hide{opacity:0;z-index:0;left:-100px}.header .header-items .header-search{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;padding:0 20px;padding-right:40px;background:inherit;color:#fff;height:5rem;font-size:4rem}

.layout{display:flex;flex-direction:column}.layout .layout-wrapper{padding:calc(36px + 62px) 30px 36px;background-color:#161515;height:100vh;font-size:calc(10px + 2vmin);overflow:auto}

.playCard{box-sizing:border-box;cursor:pointer}.playCard:hover .playCardImageContainer::after{content:'';position:absolute;transform:translate(-50%, -50%);top:calc(50% - 15px);left:50%;width:100%;height:100%;box-shadow:inset 0px 0px 40px 10px rgba(21,21,21,0.8)}.playCard:hover .playCardImage,.playCard:hover .playCardName{transform-origin:center;transform:scale(0.98) translateY(-15px)}.playCard .playCardImageContainer{position:relative;transition:all 0.3s ease-in-out;transform:translateZ(0)}.playCard .playCardImageContainer .playCardImage{width:100%;height:100%;margin-bottom:24px;transition:all 0.08s ease-in-out}.playCard .playCardName{color:#fff;transition:all 0.08s ease-in-out}

.listingPage{width:100%}.listingPage .listings{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:30px;grid-row-gap:90px}

*{margin:0px;padding:0px;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

